import React, { Component } from 'react';
import Box from '@mui/material/Box';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import AddHome from '@mui/icons-material/AddHome';
import EditRoadIcon from '@mui/icons-material/EditRoad';
import GoogleIcon from '@mui/icons-material/Google';
import LogoutIcon from '@mui/icons-material/Logout';

import { AuthEvents, CurrentUserState, LoginOption } from './CurrentUser';
import { Link } from 'react-router-dom';

type NavigationState = {
  isAuthenticated?: boolean,
  loginOptions?: LoginOption[],
  logoutLink?: string,
}

type NavigationProperties = {
  toggleVisibility: (show: boolean) => void,
  show: boolean
}

export class Navigation extends Component<NavigationProperties, NavigationState> {
  state: NavigationState = {};

  componentDidMount(): void {
    window.addEventListener(AuthEvents.AUTH_SET, this.onAuthSet.bind(this) as EventListener);
  }

  onAuthSet(event: CustomEvent<CurrentUserState>) {
    this.setState({
      isAuthenticated: event.detail.isAuthenticated,
      loginOptions: event.detail.loginOptions,
      logoutLink: event.detail.logoutLink
    });
  }
  // window.dispatchEvent(new CustomEvent<CurrentUserState>(AuthEvents.AUTH_SET, newState));
  onShow(event: React.KeyboardEvent | React.MouseEvent) {

      if (
        event &&
        event.type === 'keydown' &&
        ((event as React.KeyboardEvent).key === 'Tab' ||
          (event as React.KeyboardEvent).key === 'Shift')
      ) {
        return;
      }
      this.props.toggleVisibility(true);
  }

  onClose(event: React.KeyboardEvent | React.MouseEvent) {

      if (
        event &&
        event.type === 'keydown' &&
        ((event as React.KeyboardEvent).key === 'Tab' ||
          (event as React.KeyboardEvent).key === 'Shift')
      ) {
        return;
      }
      this.props.toggleVisibility(false);
  }

  render(): React.ReactNode {
    return  <React.Fragment>
      <SwipeableDrawer
        anchor='left'
        open={this.props.show}
        onClose={this.onClose.bind(this)}
        onOpen={this.onShow.bind(this)}
    >
       <Box 
       sx={{ width: 250 }}
      role="presentation"
      onClick={this.onClose.bind(this)}
      onKeyDown={this.onClose.bind(this)}
      >
        {!!this.state.isAuthenticated && (
          <List>
            <ListItem key="home" disablePadding>
              <ListItemButton 
                component={Link}
                to='/'
                >
                <ListItemIcon><AddHome /></ListItemIcon>
                <ListItemText primary="Home" />
              </ListItemButton>

            </ListItem>
            <ListItem key="plan" disablePadding>
              <ListItemButton
                component={Link}
                to='plan/'
                >
                <ListItemIcon><EditRoadIcon /></ListItemIcon>
                <ListItemText primary="Plan" />
              </ListItemButton>
            </ListItem>
            <Divider />
            <ListItem key="logout" disablePadding>
              <ListItemButton href={this.state.logoutLink || ''}>
                <ListItemIcon><LogoutIcon /></ListItemIcon>
                <ListItemText primary="Logout" />
              </ListItemButton>
            </ListItem>
          </List>
        )}
        
        {!this.state.isAuthenticated && !!this.state.loginOptions && (
        <List>
          <Divider />
          {this.state.loginOptions.map((item) => (
            <ListItem key={item.provider} disablePadding>
            <ListItemButton href={item.link}>
              {/* TODO: make icon dynamic  */}
              <ListItemIcon><GoogleIcon /></ListItemIcon> 
              <ListItemText primary={'Login with ' + item.name} />
            </ListItemButton>
          </ListItem>
          ))}
          
        </List>
        )}
        
      </Box>
    </SwipeableDrawer>
  </React.Fragment>;
  }
}
