import React, { Component } from 'react';
// import fetch, { Headers } from 'node-fetch';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import Box from  '@mui/material/Box';
import  TextField from '@mui/material/TextField';
import Cookies from 'js-cookie';
import { enqueueSnackbar } from 'notistack';

type AddExpenseFormProp = {
  baseUrl: string
}
type ErrorState = {
  messages: string[]
}

type FormErrorState = {
  total: ErrorState
}

type AddExpenseFormState =  {
  total: number,
  details?: string,
  error?: FormErrorState,
  alertMessage?: string
}
// const enquireSnackBar = useSnackbar();
export class AddExpenseForm extends Component<AddExpenseFormProp, AddExpenseFormState> {
  defaultState : AddExpenseFormState =  {
    total: 0,
    details: '',
  }
  state: AddExpenseFormState = this.defaultState;

  // async componentDidMount() {
  // }
  handleTotalChange(event: React.ChangeEvent<HTMLInputElement> ) {
    this.setState({total: parseFloat(event.target.value)});
  }

  handleDetailsChange(event: React.ChangeEvent<HTMLInputElement> ) {
    this.setState({details: event.target.value});
  }
  
  async createExpense() {
    const response = await fetch(
      this.props.baseUrl + 'api/exp/', 
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json', 
          'X-CSRFToken': Cookies.get('csrftoken') ?? '',
        },
        body: JSON.stringify({
          total: this.state.total,
          summary: this.state.details,
        })
      }
    );
    if (response.status === 400) {
      const jsonResp  = await response.json();
      this.setState({error: {total: {messages: jsonResp.total}}})
    } else if (response.status === 201){
      this.setState({error: undefined})
      const jsonResp  = await response.json();
      console.log(jsonResp);
      this.setState(this.defaultState)
      enqueueSnackbar('Expense of $' + jsonResp.total + ' added', { variant: 'success' });
      // enqueueSnackbar('Unexpected response ' + response.status + '', { variant: 'error' });
      window.dispatchEvent(new CustomEvent('new-expense-added'));
    } else {
      console.error('unexpected response');
      console.error(response.status);
      console.error( await response.json());
      enqueueSnackbar('Unexpected response ' + response.status + '', { variant: 'error' });
    }
  }
  handleSubmit(event: React.FormEvent<HTMLInputElement>) {
    event.preventDefault();
    this.createExpense();
  }
  totalError(): boolean {
    return (this.state.error && this.state.error.total.messages.length > 0) || false
  }

  render(): React.ReactNode {
    return  <Box
    component="form" onSubmit={this.handleSubmit.bind(this)}
    sx={{
      display: 'flex',
      alignItems: 'left',
      '& > :not(style)': { m: 1 },
    }}
  >
        <FormControl error={this.totalError()}>
          <TextField error={this.totalError()} id="total" label="total" aria-describedby="total-helper" required={true} type="number" 
            inputProps={{min:0.01,  step:0.01}} value={this.state.total} onChange={this.handleTotalChange.bind(this)}/> {/** step="10%" ? */}
          <FormHelperText id="total-helper">
            {this.state.error && this.state.error.total.messages.length > 0 ?(
              this.state.error.total.messages.join(',')
            ):(
              "total price"
            )}
            
          </FormHelperText>
        </FormControl>

        <FormControl>
          <TextField id="expense-details" label="Details" aria-describedby="expense-details-helper" type="text" 
            multiline={true} minRows="2" value={this.state.details} onChange={this.handleDetailsChange.bind(this)}/> 
          <FormHelperText id="expense-details-helper">details  describing the expense</FormHelperText>
        </FormControl>

          <Button variant="contained" color="primary" type="submit">
              Add
          </Button>
      </Box>
  }
}

