import React from 'react';
import { SnackbarProvider } from 'notistack';
import {
  createBrowserRouter,
  RouterProvider,
  // Link,
  // Outlet - should hold  the current path 
} from "react-router-dom";


import './App.css';
import { Index } from './components/routes/Index';
import { Plan } from './components/routes/Plan';
import { CurrentUser } from './components/CurrentUser';
import { Root } from './components/routes/Root';



const baseUrl = process.env.NODE_ENV === 'development' ? '/django' : '';

const router = createBrowserRouter([
  {
    path: "/",
    element:<Root baseUrl={baseUrl} />,
    children: [
      {
        path: "",
        element: <Index baseUrl={baseUrl} />
      },
      {
        path: "plan/",
        element: <Plan baseUrl={baseUrl} />
      },

    ]
  },
  
]);

function App() {
  return (<div>
    <CurrentUser baseUrl={baseUrl}/>
    <SnackbarProvider>
      <RouterProvider router={router} />
    </SnackbarProvider>
  </div>);
}

export default App;