import React, { Component } from 'react';

import Box from  '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

type DataEntry = {
  id: string,
  total:  number,
  summary: string,
}
type DataList = DataEntry[]

type RecentExpensesState = {
  recentExpenses: DataList
}

type RecentExpensesProperties = {
  baseUrl: string
}

export class RecentExpenses extends Component<RecentExpensesProperties, RecentExpensesState> {
  state : RecentExpensesState =  {
    recentExpenses: []
  }

  async resetData() {
    const res  = await fetch(this.props.baseUrl + '/api/exp/');
    const respBody = await res.json();
    console.log(respBody);
    this.setState({
      recentExpenses: respBody.results
    });
  }
  async componentDidMount(): Promise<void> {
    window.addEventListener('new-expense-added', ((event: CustomEvent) => {
      this.resetData()
   }) as EventListener);

    await this.resetData()
  }

  render(): React.ReactNode {
    return  <Box>
      <TableContainer component={Paper}>
      <Table aria-label="Recent Expenses">
        <TableHead>
          <TableRow>
            <TableCell>Total</TableCell>
            <TableCell >Summary</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {this.state.recentExpenses.map((row) => (
            <TableRow
              key={row.id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.total}
              </TableCell>
              <TableCell >{row.summary}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </Box>
  }
}

