import React, { Component } from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MenuItem from '@mui/material/MenuItem';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import Link from '@mui/material/Link';

import  { Navigation } from '../Navigation';
import { AuthEvents, CurrentUserState } from '../CurrentUser';

type AppHeaderProperties = {
  baseUrl: string,
}

type AppHeaderState = {
  isLoaded: boolean,
  isAuthenticated: boolean | null,
  username: string | undefined,
  logoutLink: string | null,
  avatar: string | undefined,
  anchorEl: null | HTMLElement,
  showNav?: boolean,
}

export class AppHeader extends Component<AppHeaderProperties, AppHeaderState> {
  state: AppHeaderState = {
    isLoaded: false,
    isAuthenticated: null,
    username: undefined,
    avatar: undefined,
    logoutLink: null,
    anchorEl: null,
  }

  async componentDidMount(): Promise<void> {
    console.log('AppHeader:componentDidMount');
    window.addEventListener(AuthEvents.AUTH_SET, this.onAuthSet.bind(this) as EventListener);
  }

  onAuthSet(event: CustomEvent<CurrentUserState>) {
    const eventDetail = event.detail;
    const newState: Partial<AppHeaderState> = {
      isLoaded: true,
      isAuthenticated: eventDetail.isAuthenticated ?? false,
      username: eventDetail.user?.username,
      avatar: eventDetail.user?.avatar,
      logoutLink: eventDetail.logoutLink ?? null
    }
    this.setState({...this.state, ...newState});
  }

  setAnchorEl(element: null | HTMLElement ) {
    this.setState({
      ...this.state,
      anchorEl: element
    })
  }

  handleMenu(event: React.MouseEvent<HTMLElement>) {
    this.setAnchorEl(event.currentTarget);
  }
  
  handleClose() {
    this.setAnchorEl(null);
  }
  handleNavigation() {
    // todo: research react on how to send event to child navigation component
    console.log('handleNavigation');
    this.toggleNavVisibility(true);
  }
  toggleNavVisibility(show: boolean) {
    this.setState({
      ...this.state,
      showNav: show
    });
  }

  render(): React.ReactNode {
    return  <Box sx={{ flexGrow: 1 }}>
          <Navigation toggleVisibility={this.toggleNavVisibility.bind(this)} show={!!this.state.showNav} />
          <AppBar position="static">
            <Toolbar>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2 }}
                onClick={this.handleNavigation.bind(this)}
              >
                <MenuIcon />
               </IconButton>
               <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                 Expenser
               </Typography>
               {this.state.isAuthenticated && (
                <div>
               <IconButton
                size="large"
                aria-label={"Logged in as" + this.state.username}
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={this.handleMenu.bind(this)}
                color="inherit"
              >
                {this.state.avatar 
                  ? <Avatar alt={this.state.username} src={this.state.avatar} />
                  : <AccountCircle />
                }
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={this.state.anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(this.state.anchorEl)}
                onClose={this.handleClose.bind(this)}
              >
                <MenuItem component={Link} href={this.state.logoutLink || ''}> Logout {this.state.username}</MenuItem>
              </Menu>
              </div>
               )}
            </Toolbar>
          </AppBar>
        </Box>
  }
}
