import {
  Outlet
} from 'react-router-dom';
import { AppHeader } from '../header/AppHeader';
import { LoginBox } from '../header/LoginBox';

type RootProp = {
  baseUrl: string;
}; 

export function Root({baseUrl}: RootProp) {
  return (<>
        <AppHeader baseUrl={baseUrl} />
        <LoginBox baseUrl={baseUrl}>
          <Outlet/>
        </LoginBox>
  </>
  )
}