import React, { Component } from 'react';
import Button from '@mui/material/Button';
import { AuthEvents, CurrentUserState, LoginOption } from '../CurrentUser';

type  LoginProvider = {
  provider: string,
  name: string,
  link: string,
}

type LoginBoxState = {
  providers: LoginOption[]
  isAuthenticated: boolean | null
}

type LoginBoxProperties = {
  baseUrl: string | null,
  children?: React.ReactNode,
}

export class LoginBox extends Component<LoginBoxProperties, LoginBoxState> {
  state: LoginBoxState = {
    providers: [],
    isAuthenticated: null
  }

  async componentDidMount(): Promise<void> {
    window.addEventListener(AuthEvents.AUTH_SET, this.onAuthSet.bind(this) as EventListener);
  }

  onAuthSet(event: CustomEvent<CurrentUserState>) {
    console.log('LoginBix: onAuthSet')
    const newState: LoginBoxState = {
      isAuthenticated: event.detail.isAuthenticated ?? false,
      providers: event.detail.loginOptions ?? [],
    }
    console.log(newState);
    this.setState(newState);
  }

  render(): React.ReactNode {
    return <div>
      {this.state.isAuthenticated ? 
      (
        this.props.children
      ) : (
        this.state.providers.map(
          (provider: LoginProvider) => <Button key={provider.provider} variant="contained" href={provider.link} >  {provider.name}</Button>
        )
      )}
    </div>
  }
}

