import React, { Component } from 'react';


export enum AuthEvents {
  AUTH_SET =  'expenser.auth.set',
}

enum SocialProvider {
  GOOGLE = 'google-oauth2',
  //SMTH_ELSE = 'smth_else'
}

type CurrenUserSocialAuth = {
  provider: SocialProvider,
  uid: string,
  avatar: string,
}

type CurrentUserModel = {
  username: string,
  avatar: string,
  socialAuth: CurrenUserSocialAuth[],
}

export type LoginOption = {
  provider: SocialProvider,
  name: string,
  link: string,
}

export type CurrentUserState = {
  user?: CurrentUserModel,
  loginOptions?: LoginOption[],
  logoutLink?: string,
  isAuthenticated?: boolean,
}

type CurrentUserProperties = {
  baseUrl: string
}

export class CurrentUser extends Component<CurrentUserProperties, CurrentUserState> {
  state : CurrentUserState =  {
  }

  componentDidMount(): void {
    console.log('CurrentUser:componentDidMount:props:' + this.props.baseUrl);
    this.loadCurrentUser()
  }

  async loadCurrentUser(): Promise<void> {
    const res  = await fetch(this.props.baseUrl + '/u/');
    const respBody = await res.json();
    var newState: CurrentUserState =  {
      loginOptions: respBody.login
    }; 

    if (respBody.is_authenticated) {
      newState.isAuthenticated = true;
      newState.user = {
        username: respBody.user.username, 
        avatar: respBody.user.social_auth[0].avatar,
        socialAuth: respBody.user.social_auth,

      };
      newState.logoutLink = respBody.logout;
    }
    this.setState(newState);
    window.dispatchEvent(new CustomEvent<CurrentUserState>(AuthEvents.AUTH_SET, {detail: newState}));
  }


  render(): React.ReactNode {
    return (null);
  }
}